










import Vue from 'vue';

export default Vue.extend({
    name: 'Settings',
    data: () => ({}),
    async mounted() {
    },
    computed: {
        socketUrl: {
            get(): string {
                return this.$store.state.url;
            },
            set(v: string) {
                this.$store.commit('socketUrl', v);
            },
        }
    },
});
